@mixin base() {
    @include containerPadding($vertical-multiplier: 4);   

    background-color: $component-content-secondary-background;
    text-align: center;

    @media (min-width: 801px) {
        @include containerPadding($vertical-multiplier: 6);   
    }

}

.banner {
    @include base;

    @import "heading";
    @import "content";
    @import "actions";
}