//Uses button base mixin from the button/_base.scss file
@import "./base";

//Where should this live?
button {
    @include base();
}
//

.button {
    @include base();

    &--primary {
        @include base(primary);
    }

    &--disabled {
        @include base(disabled);
    }

    &--positive {
        @include base(positive);
    }

    &--negative {
        @include base(negative);
    }
}