@import "../../../mixins/clickable";

//button-base takes a string that is used by the clickable mixin
@mixin button-with-icon-base($feedback-type: "neutral") {
    //clickable mixin and feedback type determine styles
    @include clickable($feedback-type);
    @include rhythmBorder($horizontal-multiplier: 2);   
    
    //sets standard button styles
    border: none;
    border-radius: 5px;        
    display: inline-block;
    text-transform: uppercase;

    //background-image: url("http://fillmurray.com/100/100");
    background-repeat: no-repeat;
    background-size: 25px;

    padding: 0 25px;

    img {
        width: 78px;
        height: 78px;
    }
}