@mixin base() {
    display: flex;
    flex-wrap: wrap;
    font-size: 0; // remove whitespace between list items
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;

    @media (min-width: 801px) {
        flex-wrap: nowrap;
    }
}

&__list {
    @include base;
}