@mixin base() {
    background-color: transparent;
    border-radius: 0;
    color: $component-content-text;
    padding: 15px;
    position: absolute;
    right: 0;
    top: 0;
    text-transform: uppercase;

    &:hover {
        background-color: transparent;
        color: $component-content-text;
    }
}

&__close {
    @include base;
}