@mixin base() {
    flex-basis: 100%;
    flex-grow: 1;
    flex-shrink: 1;
    margin: 10px 0;

    @media (min-width: 801px) {
        flex-basis: calc(50% - 10px);
        flex-grow: 0;
    }

    @media (min-width: 1150px) {
        flex-basis: calc(33.3333% - 10px);
    }
}

&__item {
    @include base;

    display: flex;

    article {
        @include containerPadding($horizontal-multiplier: 3);

        background-color: $component-content-secondary-background;
        display: flex;
        flex-basis: 100%;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;
    }
}