//Uses button base mixin from the button/_base.scss file
@import "./base";

.button-with-icon {
    @include button-with-icon-base();

    &--primary {
        @include button-with-icon-base(primary);
    }

    &--disabled {
        @include button-with-icon-base(disabled);
    }

    &--positive {
        @include button-with-icon-base(positive);
    }

    &--negative {
        @include button-with-icon-base(negative);
    }
}