@mixin base {
    background-color: $component-content-inverse-background;
    color: $component-content-inverse-text;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.page-footer {
    @include base;

    @import "branding";
    @import "content";
    @import "copyright";
    @import "navigation";
}