&__link {
    @include rhythmBorder($horizontal-multiplier: 2);

    color: $component-content-inverse-text;
    text-decoration: none;
    display: block;

    &:hover {
        background-color: rgba(255, 255, 255, 0.1);
        opacity: 0.6;
    }

    &:focus {
        background-color: rgba(0, 0, 0, 0.2);
    }

    &:active {
        background-color: rgba(0, 0, 0, 0.3);
    }
}